import { createRoot } from 'react-dom/client'
import React, { useRef, useState } from 'react'
import { Canvas, useFrame,useLoader  } from '@react-three/fiber'
import { TextureLoader } from 'three/src/loaders/TextureLoader'


function Box(props) {


  // This reference gives us direct access to the THREE.Mesh object
  const ref = useRef()
  // Hold state for hovered and clicked events
  const [hovered, hover] = useState(false)
  const [clicked, click] = useState(false)
  // Subscribe this component to the render-loop, rotate the mesh every frame
  useFrame((state, delta) => (ref.current.rotation.x += (props.speed / 20) ))
  // Return the view, these are regular Threejs elements expressed in JSX
  return (
    <mesh
      {...props}
      ref={ref}
      scale={clicked ? 1.2 : 1}
      onPointerEnter={(event) => click(!clicked)}
      onPointerOver={(event) => hover(true)}
      onPointerOut={(event) => hover(false)}>
      <boxGeometry args={[1,1,1,props.vertices,props.vertices,props.vertices]} />
      <meshPhysicalMaterial color={clicked ? 'hotpink' : 'blue'} wireframe={props.wireframe} />
    </mesh>
  )
}


export default Box;